import swiperImage from '@/components/index/swiper-image.vue';
import card from "../../components/common/card.vue"
import loading from "../../components/common/minxin/loading.js"
import unListItem from "../../components/uni-ui/uni-list-item/uni-list-item.vue"
import { mapState,mapMutations,mapGetters } from "vuex"
export default {
	mixins:[loading],
	components:{
		card,unListItem,swiperImage
	},
	data() {
		return {	
			src:true,
			input:"",
			myList:[
				{
					title:"待付款",
					icon:"icon-daifukuan",
					index:1
				},
				{
					title:"待收货",
					icon:"icon-daishouhuo1",
					index:2
				},
				{
					title:"待评价",
					icon:"icon-pinglun",
					index:3
				},
				{
					title:"优惠券",
					icon:"",
					index:""
				}
			],
			swiperData:[]//轮播广告数据
		}
	},
	onShow() {
		// this.$update(this.getGoods)
	},
	computed:{
		...mapGetters(['getGoods']),
		...mapState({
			userInfo:state=>state.user.userInfo,
			loginStatus:state=>state.user.loginStatus
		})
	},
	onLoad() {
		this.getData()
	},
	methods: {
		...mapMutations(['change']),
		selectTab(img){
			
		},
		getData(){
			// 获取轮播图数据
			this.$R.get("/goods/banners/").then((res) => {
				console.log(JSON.stringify(res))
				if (res) {
					this.swiperData = res
				}
			})
		},
		openLogin(){
			// 存在登录状态修改昵称 不存在登录页面
			if(this.loginStatus){
				// 修改昵称
				// uni.navigateTo({
				// 	url:"../login/login"
				// })
			}else{
				uni.navigateTo({
					url:"../login/login"
				})
			}
			
		},
		// 跳转订单
		openOrder(index){
			if(index){
				this.navigateTo({
					url:"/pages/order/order?index="+index
				})
			}else{
				this.navigateTo({
					url:"/pages/order-coupon/order-coupon"
				})
			}
		},
		navigate(path,check= false){
			if (!path) return;
			if(check){
				return this.navigateTo({
					 url:`/pages/${path}/${path}`
				})
			}
			uni.navigateTo({
				url:`/pages/${path}/${path}`
			})
		}
	},
	onReady() {
		this.beforeReady = false
	}
}
